.asl-photos-container {
    background-color: #2d8bba; /* Blue background */
    color: #ffffff; /* White text */
    padding: 40px;
    text-align: center;
}

.asl-header {
    font-size: 2.5rem;
    margin-bottom: 30px;
    font-family: 'Crimson Pro', serif;
}

.photos-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    align-items: center;
}

.photo {
    flex: 1 1 calc(33.33% - 20px); /* Responsive grid layout */
    max-width: 400px;
    border-radius: 15px;
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.photo-img {
    width: 100%;
    height: auto;
    border-radius: 15px;
}


.asl-description-section {
    background-color: #e7e2de;
    color: #333333; 
    text-align: left;
    line-height: 1.5;
    border-radius: 15px;
    margin-top: 20px;
    font-family: 'Crimson pro';
}

.description {
    margin-bottom: 20px;
    font-size: 1.2rem;
    margin: 5% 5%;
}