body {
    margin: 0;
    padding: 0;
    font-family: 'Montserrat', Arial, Helvetica, sans-serif;
    width: 100%
}

.header {
    background-color: transparent;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    width: 100%;
    top: 0;
    z-index: 100;
}

.logo {
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
}


.logo-img {
    height: 80px;
}

.logo-text {
    color: #2d8bba;
    margin: 0;
}

.home-page {
    background-size: cover;
    background-position: center;
    min-height: 100vh;
    margin: 0;
    padding: 0;
    background-color: transparent;
}

.hero {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 80px;
    position: relative;
}

.title-year-container {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    line-height: 1;
    width: 100%;
}

.title {
    font-size: 120px;
    color: #2d8bba;
}

.title span {
    display: block;
}

.year {
    font-size: 120px;
    color: #ffffff;
    align-self: flex-start;
    line-height: 1;
}

.date-container {
    cursor: pointer;
    text-align: center;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    transition: transform 0.3s ease, background-color 0.3s ease;
}

.date-container:hover {
    transform: scale(1.05);
    color: #ffffff;
}

.date {
    font-size: 24px;
    padding: 10px 20px;
    border: 2px solid #2d8bba;
    border-radius: 50px;
    display: inline-block;
    color: #2d8bba;
    transition: color 0.2s ease, border-color 0.2s ease;
}

.date-container:hover .date {
    color: #ffffff;
    border-color: #ffffff;
}


@media (max-width: 600px) {

    body {
        background-color: #2d8bba;
    }

    .header {
        flex-direction: column;
        align-items: center;
    }

    .logo-img {
        height: 50px;
    }

    .logo-text {
        font-size: 18px;
        color: #ffffff;
    }

    .nav {
        flex-direction: column;
        gap: 10px;
        margin: 10px 0;
    }

    .nav-link {
        font-size: 18px;
    }

    .home-page {
        background-image: none;
    }

    .hero {
        padding: 20px;
        align-items: center;
    }

    .title-year-container {
        flex-direction: column;
        align-items: center;
    }

    .title {
        font-size: 48px;
        text-align: center;
        color: #ffffff;
    }

    .year {
        font-size: 48px;
        text-align: center;
        color: #ffffff;
    }

    .date-container {
        margin-top: 15px;
    }

    .date {
        font-size: 20px;
        padding: 8px 15px;
    }
}